import React from 'react';
import {Redirect, Route, Switch} from 'react-router-dom';
import asyncComponent from '../../../util/asyncComponent';

const TaskManager = ({match}) => (
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/my-tasks`}/>
      <Route path={`${match.url}/site`} component={asyncComponent(() => import('./routes/site'))}/>
      <Route path={`${match.url}/department`} component={asyncComponent(() => import('./routes/department'))}/>
      <Route component={asyncComponent(() => import('app/routes/extraPages/routes/404'))}/>
    </Switch>
);

export default TaskManager;