import axios from "axios";

const base_url = "https://10.10.10.207/api/";
const public_url = "https://10.10.10.207/";
const sticker_url = "https://10.10.10.207/pixie/assets/images/stickers/";
const $http = axios.create({
    baseURL: base_url,
    timeout: 5000,
    headers: {
        'X-Auth-Token': localStorage.getItem("token"),
        "Content-Type": "application/json; multipart/form-data"
    }
})

$http.interceptors.response.use((response) => {
    if (response.data.status === "TokenError") {
        //perform the manipulation here and change the response object
        //alert("You are not authorized");
        localStorage.removeItem('user_id');
    }
    return response;
}, (error) => {
    //console.log(error)
    return Promise.reject(error.message);
});

const config = {
    base_url: base_url
}
export default config;
export {base_url, public_url, sticker_url, $http}

