import axios from "axios";
import {base_url} from "../config";

const createUserWithEmailPasswordRequest = async (
    email, password, company_name, user_name, 
    website, company_email, address, city, postcode, 
    first_name, last_name, job_title, mobile, profile_pic, logo_img) =>{
    const user = {email: email, password: password, company_name: company_name, 
        user_name: user_name, website: website, company_email: company_email, 
        address: address, city: city, postcode: postcode, first_name: first_name, 
        last_name: last_name, job_title: job_title, mobile: mobile, 
        profile_pic: profile_pic, logo_img: logo_img};
    const userData = new FormData();
    userData.append('email', email);
    userData.append('password', password);
    userData.append('company_name', company_name);
    userData.append('user_name', user_name);
    userData.append('website', website);
    userData.append('company_email', company_email);
    userData.append('address', address);
    userData.append('city', city);
    userData.append('postcode', postcode);
    userData.append('first_name', first_name);
    userData.append('last_name', last_name);
    userData.append('job_title', job_title);
    userData.append('mobile', mobile);
    userData.append('profile_pic', profile_pic);
    userData.append('logo_img', logo_img);
    

    return new Promise((resolve, reject)=> {
        axios.post(base_url + "user/register", userData)
        .then((response) => {
            //console.log("register:", response.data);
            if (response.data.status==="success") {
                //resolve({message: response.data.msg});
                NotificationManager.success(response.data.msg, "Register Success");
                resolve(user);
            }
            else {
                resolve({message: response.data.msg});
            }
        })
        .catch((error)=> error)
    })
}
const signInUserWithEmailPasswordRequest = async (email, password) => {
    const user = {email: email, password: password};
    return new Promise((resolve, reject)=> {
        axios.post(base_url + "user/login", user)
        .then((response) => {

            if (response.data.status==="success") {
                localStorage.setItem('token', response.data.data.auth_token);
                localStorage.setItem('user', JSON.stringify(response.data.data));
                resolve(user);
            }
            else {
                resolve({message: response.data.msg});
            }
        })
        .catch((error)=> error)
    })
    
}
