function* createUserWithEmailPassword({payload}) {
    const {email, password, company_name, user_name, 
        website, company_email, address, city, postcode, 
        first_name, last_name, job_title, mobile, profile_pic, logo_img} = payload;
    try {
        const signUpUser = yield call(createUserWithEmailPasswordRequest, 
            email, password, company_name, user_name, 
            website, company_email, address, city, postcode, 
            first_name, last_name, job_title, mobile, profile_pic, logo_img);
        //console.log(signUpUser);
        if (signUpUser.message) {
            yield put(showAuthMessage(signUpUser.message));
        } else {
            // localStorage.setItem('user_id', signUpUser.email);
            yield put(userSignUpSuccess(signUpUser.email));
        }
    } catch (error) {
        yield put(showAuthMessage(error));
    }
}

function* signInUserWithEmailPassword({payload}) {
    const {email, password} = payload;
    try {
        const signInUser = yield call(signInUserWithEmailPasswordRequest, email, password);
        //console.log(signInUser);
        if (signInUser.message) {
            yield put(showAuthMessage(signInUser.message));
        } else {
            localStorage.setItem('user_id', signInUser.email);
            yield put(userSignInSuccess(signInUser.email));
        }
    } catch (error) {
        yield put(showAuthMessage(error));
    }
}

