import React from 'react';
import CustomScrollbars from 'util/CustomScrollbars';
import Navigation from "../../components/Navigation";
// import { Collapse } from '@material-ui/core';

const SideBarContent = () => {
    const navigationMenus = [
        {
            name: 'sidebar.dashboard',
            icon: 'view-dashbaord',
            type: 'item',
            link: '/app/dashboard'
        },
        {
            name: 'sidebar.customers',
            icon: 'account-circle',
            type: 'collapse',
            children: [
                {
                    name: 'sidebar.customers.company',
                    icon: 'balance',
                    type: 'item',
                    link: '/app/customers/company'
                },
                {
                    name: 'sidebar.customers.users',
                    icon: 'account',
                    type: 'item',
                    link: '/app/customers/users'
                },
                // {
                //     name: 'sidebar.customers.pendinginvites',
                //     icon: 'email',
                //     type: 'item',
                //     link: '/app/customers/pending-invites'
                // }
            ]
        },
        {
            name: 'sidebar.taskmanager',
            icon: 'format-list-bulleted',
            type: 'collapse',
            children: [
                {
                    name: 'sidebar.taskmanager.addnew',
                    icon: 'plus-circle',
                    type: 'item',
                    link: '/app/task-manager/add-new'
                },
                {
                    name: 'sidebar.taskmanager.mytasks',
                    icon: 'check-square',
                    type: 'item',
                    link: '/app/task-manager/my-tasks'
                },
                {
                    name: 'sidebar.sitesurvey.archived',
                    icon: 'archive',
                    type: 'item',
                    link: '/app/task-manager/archived'
                }
            ]
        },
        {
            name: 'sidebar.sites',
            icon: 'widgets',
            type: 'collapse',
            children: [
                {
                    name: 'sidebar.sites.site',
                    icon: 'widgets',
                    type: 'item',
                    link: '/app/sites/site'
                },
                {
                    name: 'sidebar.sites.departments',
                    icon: 'check-square',
                    type: 'item',
                    link: '/app/sites/department'
                }
            ]
        },
        {
            name: 'sidebar.sitesurvey',
            icon: 'cutlery',
            type: 'collapse',
            children: [
                {
                    name: 'sidebar.sitesurvey.addnew',
                    icon: 'plus-circle',
                    type: 'item',
                    link: '/app/sitesurvey/add-new'
                },
                {
                    name: 'sidebar.sitesurvey.live',
                    icon: 'wifi-alt',
                    type: 'item',
                    link: '/app/sitesurvey/live'
                },
                {
                    name: 'sidebar.sitesurvey.archived',
                    icon: 'archive',
                    type: 'item',
                    link: '/app/sitesurvey/archived'
                }
            ]
        },
        {
            name: 'sidebar.stickers',
            icon: 'comment-edit',
            type: 'collapse',
            children: [
                {
                    name: 'sidebar.stickers.addnew',
                    icon: 'plus-circle',
                    type: 'item',
                    link: '/app/stickers/add-new'
                },
                {
                    name: 'sidebar.stickers.categories',
                    icon: 'group',
                    type: 'item',
                    link: '/app/stickers/categories'
                }
            ]
        },
        {
            name: 'sidebar.settings',
            icon: 'settings',
            type: 'collapse',
            children: [
                {
                    name: 'sidebar.settings.profile',
                    icon: 'account',
                    type: 'item',
                    link: '/app/settings/profile'
                },
                {
                    name: 'sidebar.settings.users',
                    icon: 'account',
                    type: 'item',
                    link: '/app/settings/users'
                },
                {
                    name: 'sidebar.customers.company',
                    icon: 'blur-linear',
                    type: 'item',
                    link: '/app/settings/company-settings'
                },
                {
                    name: 'sidebar.settings.notifications',
                    icon: 'notifications',
                    type: 'item',
                    link: '/app/settings/notifications'
                }
            ]
        },
        {
            name: 'popup.logout',
            icon: 'open-in-new',
            type: 'item',
            link: '/app/logout'
        }
    ];

    return (
        <CustomScrollbars className=" scrollbar">
            <Navigation menuItems={navigationMenus}/>
        </CustomScrollbars>
    );
};

export default SideBarContent;
