import React, {useEffect, useState} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import TextField from '@material-ui/core/TextField';
//import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import CircularProgress from '@material-ui/core/CircularProgress';
import {Link} from 'react-router-dom';
import IntlMessages from 'util/IntlMessages';
import AttachFile from '@material-ui/icons/AttachFile';
import {
    hideMessage,
    showAuthLoader,
   /*  userFacebookSignIn,
    userGithubSignIn,
    userGoogleSignIn, */
    userSignUp,
    /* userTwitterSignIn */
} from 'actions/Auth';
//import {useDropzone} from 'react-dropzone';

const SignUp = (props) => {

    const [first_name, setFirstName] = useState("");
    const [last_name, setLastName] = useState("");
    const [job_title, setJobTitle] = useState("");
    const [mobile, setPhoneNumber] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [company_name, setCompanyName] = useState("");
    const [user_name, setUniCompanyName] = useState("");
    const [website, setCompanyWebsite] = useState("");
    const [company_email, setCompanyEmail] = useState("");
    const [city, setCity] = useState("");
    const [postcode, setPostcode] = useState("");
    const [address, setAddess] = useState("");
    const [profile_pic, setProfilePic] = useState(null);
    const [logo_img, setLogoImg] = useState(null);
    const [activeStep, setActiveStep] = useState(0);
    
    const dispatch = useDispatch();
    const {loader, alertMessage, showMessage, authUser} = useSelector(({auth}) => auth);

    useEffect(() => {
        if (showMessage) {
            setTimeout(() => {
                dispatch(hideMessage());
            }, 3000);
        }
        if (authUser !== null && authUser !== undefined) {
            props.history.push('/');
        }
    }, [showMessage, authUser, props.history, dispatch]);

    return (
        <div
            className="app-login-container d-flex justify-content-center align-items-center animated slideInUpTiny animation-duration-3">
            <div className="app-login-main-content">
                <div className="app-logo-content d-flex align-items-center justify-content-center">
                    <Link className="logo-lg" to="/" title="Jambo">
                        <img src={require("assets/images/logo.png")} alt="sirvez" title="sirvez"/>
                    </Link>
                </div>

                <div className="app-login-content">
                    <div className="app-login-header">
                        <h1>Sign Up (Step {activeStep + 1} / 2)</h1>
                    </div>

                    <div className="app-login-form">
                        <form method="post" action="/">
                            <div style={{display: activeStep ? "none": "block"}}>
                                <TextField
                                    type="text"
                                    label="First Name"
                                    onChange={(event) => setFirstName(event.target.value)}
                                    fullWidth
                                    defaultValue={first_name}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="Last Name"
                                    onChange={(event) => setLastName(event.target.value)}
                                    fullWidth
                                    defaultValue={last_name}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="Job Title"
                                    onChange={(event) => setJobTitle(event.target.value)}
                                    fullWidth
                                    defaultValue={job_title}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="Phone Number"
                                    onChange={(event) => setPhoneNumber(event.target.value)}
                                    fullWidth
                                    defaultValue={mobile}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="email"
                                    onChange={(event) => setEmail(event.target.value)}
                                    label={<IntlMessages id="appModule.email"/>}
                                    fullWidth
                                    defaultValue={email}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="password"
                                    onChange={(event) => setPassword(event.target.value)}
                                    label={<IntlMessages id="appModule.password"/>}
                                    fullWidth
                                    defaultValue={password}
                                    margin="normal"
                                    className="mt-0 mb-4"
                                />

                                <div className="mb-3 d-flex align-items-center justify-content-between">
                                    <input type="file" id="profile_img" accept="image/*" 
                                        name="profile_img" style={{display: "none"}}
                                        onChange={(event) => setProfilePic(event.target.files[0])}/>
                                    <Button className="attach-file jr-btn text-muted" 
                                        variant="contained" onClick={(e)=>{document.getElementById('profile_img').click()}}>
                                        <AttachFile/> Profile Picture
                                    </Button>
                                    <Button variant="contained" onClick={() => {
                                        setActiveStep(1);
                                    }} color="primary">
                                        <IntlMessages
                                            id="appModule.next"/>
                                    </Button>
                                </div>

                            </div>

                            <div style={{display: activeStep ? "block": "none"}}>

                                <TextField
                                    type="text"
                                    label="Company Name"
                                    onChange={(event) => setCompanyName(event.target.value)}
                                    fullWidth
                                    defaultValue={company_name}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="Unique Company Name"
                                    onChange={(event) => setUniCompanyName(event.target.value)}
                                    fullWidth
                                    defaultValue={user_name}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="Company Website e.g www.xyz.com"
                                    onChange={(event) => setCompanyWebsite(event.target.value)}
                                    fullWidth
                                    defaultValue={website}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="Address"
                                    onChange={(event) => setAddess(event.target.value)}
                                    fullWidth
                                    defaultValue={address}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="City"
                                    onChange={(event) => setCity(event.target.value)}
                                    fullWidth
                                    defaultValue={city}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="Postcode"
                                    onChange={(event) => setPostcode(event.target.value)}
                                    fullWidth
                                    defaultValue={postcode}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="Company Email"
                                    onChange={(event) => setCompanyEmail(event.target.value)}
                                    fullWidth
                                    defaultValue={company_email}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />
                                <div className="mb-3 d-flex align-items-center justify-content-between">
                                    
                                    <input type="file" id="logo_img" accept="image/*" 
                                        name="logo_img" style={{display: "none"}}
                                        onChange={(event) => setLogoImg(event.target.files[0])}/>
                                    <Button className="attach-file jr-btn text-muted" variant="contained" 
                                        onClick={(e)=>{document.getElementById('logo_img').click()}}>
                                        <AttachFile/> Upload Company Logo
                                    </Button>

                                    <Button variant="contained" onClick={() => {
                                        setActiveStep(0);
                                    }} color="primary">
                                        <IntlMessages
                                            id="appModule.back"/>
                                    </Button>
                                </div>

                                <div className="mb-3 d-flex align-items-center justify-content-between">
                                    <Button variant="contained" onClick={() => {
                                        dispatch(showAuthLoader());
                                        dispatch(userSignUp({email, password, company_name, user_name, 
                                            website, company_email, address, city, postcode, 
                                            first_name, last_name, job_title, mobile, 
                                            profile_pic, logo_img}));
                                    }} color="primary">
                                        <IntlMessages
                                            id="appModule.regsiter"/>
                                    </Button>
                                    <Link to="/signin">
                                        <IntlMessages id="signUp.alreadyMember"/>
                                    </Link>
                                </div>

                            </div>
                            {/* <div className="app-social-block my-1 my-sm-3">
                                <IntlMessages
                                    id="signIn.connectWith"/>
                                <ul className="social-link">
                                    <li>
                                        <IconButton className="icon"
                                                    onClick={() => {
                                                        dispatch(showAuthLoader());
                                                        dispatch(userFacebookSignIn());
                                                    }}>
                                            <i className="zmdi zmdi-facebook"/>
                                        </IconButton>
                                    </li>
                                    <li>
                                        <IconButton className="icon"
                                                    onClick={() => {
                                                        dispatch(showAuthLoader());
                                                        dispatch(userTwitterSignIn());
                                                    }}>
                                            <i className="zmdi zmdi-twitter"/>
                                        </IconButton>
                                    </li>
                                    <li>
                                        <IconButton className="icon"
                                                    onClick={() => {
                                                        dispatch(showAuthLoader());
                                                        dispatch(userGoogleSignIn());

                                                    }}>
                                            <i className="zmdi zmdi-google-plus"/>
                                        </IconButton>
                                    </li>
                                    <li>
                                        <IconButton className="icon"
                                                    onClick={() => {
                                                        dispatch(showAuthLoader());
                                                        dispatch(userGithubSignIn());
                                                    }}>
                                            <i className="zmdi zmdi-github"/>
                                        </IconButton>
                                    </li>
                                </ul>
                            </div> */}
                        </form>
                    </div>
                </div>

            </div>

            {
                loader &&
                <div className="loader-view">
                    <CircularProgress/>
                </div>
            }
            {showMessage && NotificationManager.error(alertMessage)}
            <NotificationContainer/>
        </div>
    )
};


export default SignUp;
