import React, {useState} from 'react';
import Avatar from '@material-ui/core/Avatar'
import {useDispatch} from 'react-redux'
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import {userSignOut} from 'actions/Auth';
import IntlMessages from 'util/IntlMessages';
import {public_url} from 'config';
import {useHistory} from 'react-router-dom';

const UserInfo = () => {

    const dispatch = useDispatch();
    const History = useHistory();

    const [anchorE1, setAnchorE1] = useState(null);
    const [open, setOpen] = useState(false);

    const handleClick = event => {
        setOpen(true);
        setAnchorE1(event.currentTarget);
    };

    const handleRequestClose = () => {
        setOpen(false);
    };

    const user = JSON.parse(localStorage.getItem("user"));
    //console.log("user:", user);
    return (
        <div className="user-profile d-flex flex-row align-items-center">
            <Avatar
                alt='...'
                src={public_url + 'upload/img/' + user.profile_pic}
                className="user-avatar "
            />
            <div className="user-detail">
                <h4 className="user-name d-flex" onClick={handleClick}><span
                    className='text-truncate'>{user.first_name} {user.last_name}</span> <i
                    className="zmdi zmdi-caret-down zmdi-hc-fw align-middle"/>
                </h4>
            </div>
            <Menu className="user-info"
                  id="simple-menu"
                  anchorEl={anchorE1}
                  open={open}
                  onClose={handleRequestClose}
                  PaperProps={{
                      style: {
                          minWidth: 120,
                          paddingTop: 0,
                          paddingBottom: 0
                      }
                  }}
            >
                <MenuItem onClick={() => {
                    handleRequestClose();
                    History.push('/app/settings/profile');
                }}>
                    <i className="zmdi zmdi-account zmdi-hc-fw mr-2"/>
                    <IntlMessages id="popup.profile"/>
                </MenuItem>
                <MenuItem onClick={() => {
                    handleRequestClose();
                    History.push('/app/settings/company-settings');
                }}>
                    <i className="zmdi zmdi-settings zmdi-hc-fw mr-2"/>
                    <IntlMessages id="sidebar.customers.company"/>
                </MenuItem>
                <MenuItem onClick={() => {
                    handleRequestClose();
                    dispatch(userSignOut());
                }}>
                    <i className="zmdi zmdi-sign-in zmdi-hc-fw mr-2"/>

                    <IntlMessages id="popup.logout"/>
                </MenuItem>
            </Menu>
        </div>
    );
};

export default UserInfo;


